<template>
	<div id="paotui-detail">
		<public-header title="跑腿订单详情"></public-header>
		<div class="content font-14">
			<div id="map-container"></div>
			<div class="tip">跑腿订单配送</div>
			<div class="bg-default">
				<div class="flex-lr padding-10-lr padding-15-tb van-hairline--bottom">
					<div class="c-primary font-500">#{{order.id}}</div>
					<div class="c-danger flex" v-if="order.status == 2">
						<template v-if="order.delivery_overtime > 0">
							距超时<count-down :endTime="order.delivery_overtime" class="c-danger"></count-down>
						</template>
						<template v-else-if="order.delivery_overtime_start > 0">
							已超时<count-down :startTime="order.delivery_overtime_start" class="c-danger"></count-down>
						</template>
					</div>
				</div>
				<div class="flex padding-10-lr padding-15-tb van-hairline--bottom">
					<span class="handle-type">{{order.delivery_collect_type_cn}}</span>
					<span class="delivery-time">配送 {{order.delivery_time}}</span>
				</div>
				<ul class="address padding-10-lr" margin-15-t>
					<li class="flex-lr margin-15-t" >
						<ul class="address-text buy-text">
							<li class="font-15 font-500">{{order.buy_address}}</li>
							<li class="c-gray margin-5-t font-13" v-if="order.buy_username">{{order.buy_username}} {{order.buy_mobile}}</li>
						</ul>
						<i class="icon icon-locationfill" v-if="order.buy_location_x && order.buy_location_y" @click="util.jsLocation({lat: order.buy_location_x, lng: order.buy_location_y, address: order.buy_address, type: 'app'})"></i>
					</li>
					<li class="flex-lr margin-15-t">
						<ul class="address-text accept-text">
							<li class="font-15 font-500 ">{{order.accept_address}}</li>
							<li class="c-gray margin-5-t font-13">{{order.accept_username}} {{order.accept_mobile}}</li>
						</ul>
						<i class="icon icon-locationfill" v-if="order.accept_location_x && order.accept_location_y" @click="util.jsLocation({lat: order.accept_location_x, lng: order.accept_location_y, address: order.accept_address, type: 'app'})"></i>
					</li>
				</ul>
				<div class="tel-group padding-10-lr padding-15-tb van-hairline--top margin-15-t flex-lr">
					<div class="tel-item flex van-hairline--right" v-if="order.buy_mobile" @click="util.jsTel(order.buy_mobile)">
						<div class="img-wrap">
							<img class="w-100" src="static/img/store.png" alt="">
						</div>
						<span>{{order.buy_mobile}}</span>
					</div>
					<div class="tel-item flex" @click="util.jsTel(order.accept_mobile)">
						<div class="img-wrap">
							<img class="w-100" src="static/img/customer.png" alt="">
						</div>
						<span>{{order.accept_mobile}}</span>
					</div>
				</div>
			</div>
			<div class="tip">货品信息</div>
			<div class="goods-info bg-default">
				<div class="note" v-if="order.note">
					<div class="note-title">备注:</div>
					<div class="note-text">{{order.note}}</div>
				</div>
				<div class="padding-15-b padding-5-t padding-10-lr van-hairline--bottom">
					<ul class="margin-10-t c-gray flex-lr">
						<li>物品名称</li>
						<li>{{order.goods_name}}</li>
					</ul>
					<ul class="margin-10-t c-gray flex-lr" v-if="order.box_price > 0">
						<li>物品预期价格</li>
						<li>¥{{order.goods_price}}</li>
					</ul>
					<ul class="margin-10-t c-gray flex-lr" v-if="order.goods_weight > 0">
						<li>物品重量</li>
						<li>{{order.goods_weight}}公斤</li>
					</ul>
					<template v-if="order.data && order.data.order.partData">
						<ul class="margin-10-t c-gray flex-lr" v-for="(partItem, partIndex) in order.data.order.partData">
							<template v-if="partItem.type != 'multipleChoices'">
								<li>{{partItem.title}}</li>
								<li>{{partItem.value}}</li>
							</template>
							<template v-else>
								<li>{{partItem.title}}</li>
								<li>
									<template v-for="value_child in partItem.value">
										{{' ' + value_child}}
									</template>
								</li>
							</template>
						</ul>
					</template>
				</div>
				<div v-if="order.is_pay == 1">
					<div class="have-pay" v-if="order.pay_type != 'delivery'"></div>
					<div class="delivery-pay" v-else-if="order.pay_type == 'delivery'"></div>
				</div>
			</div>
			<template v-if="order.thumbs && order.thumbs.length > 0">
				<div class="tip">顾客上传的图片</div>
				<div class="goods-imgs padding-10-lr padding-15-t padding-5-b bg-default">
					<van-row gutter="10">
						<van-col span="6" v-for="(imgItem, index) in order.thumbs" :key="index">
							<div class="img-wrap">
								<img class="wh-100" :src="imgItem" alt="">
							</div>
						</van-col>
					</van-row>
				</div>
			</template>
			<template v-if="order.data && order.data.order && order.data.order.extra_fee">
				<div class="tip">附加服务</div>
				<div class="bg-default padding-10-lr padding-5-t padding-15-b">
					<ul class="margin-10-t flex-lr" v-for="(feeItem, feeIndex) in order.data.order.extra_fee" :key="feeIndex">
						<li>{{feeItem.title}}</li>
						<li>
							<template v-for="feeChildItem in feeItem.value">
								{{' ' + feeChildItem.name}}
							</template>
						</li>
					</ul>
				</div>
			</template>
			<div class="tip">订单信息</div>
			<div class="bg-default padding-10-lr padding-5-t padding-15-b">
				<ul class="margin-10-t flex-lr">
					<li>订单编号</li>
					<li>{{order.order_sn}}</li>
				</ul>
				<ul class="margin-10-t flex-lr">
					<li>下单时间</li>
					<li>{{order.addtime_cn}}</li>
				</ul>
				<ul class="margin-10-t flex-lr">
					<li>付款方式</li>
					<li>{{order.pay_type_cn}}</li>
				</ul>
			</div>
			<div class="tip">订单收入</div>
			<div class="bg-default padding-10-lr padding-5-t padding-15-b">
				<ul class="margin-10-t flex-lr">
					<li>配送费</li>
					<li>¥{{order.deliveryer_fee}}</li>
				</ul>
				<ul class="margin-10-t flex-lr" v-if="order.delivery_tips > 0">
					<li>小费</li>
					<li>¥{{order.delivery_tips}}</li>
				</ul>
				<ul class="margin-10-t flex-lr">
					<li>本单收入</li>
					<li>¥{{order.deliveryer_total_fee}}</li>
				</ul>
			</div>
			<div class="btn-group padding-10-lr padding-15-t padding-5-b">
				<van-row gutter="10">
					<template v-if="order.transfer_deliveryer_id == deliveryer.id && order.transfer_delivery_status == 1">
						<van-col span="8">
							<div class="btn-item bg-danger" @click="onChangeOrderStatus('direct_transfer_refuse')">拒绝转单</div>
						</van-col>
						<van-col span="8">
							<div class="btn-item bg-primary" @click="onChangeOrderStatus('direct_transfer_agree')">接受转单</div>
						</van-col>
					</template>
					<template v-else-if="deliveryer.id == order.deliveryer_id">
						<van-col span="8" v-if="order.delivery_status == 3">
							<div class="btn-item bg-primary" @click="onChangeOrderStatus('delivery_success')">确认送达</div>
						</van-col>
						<van-col span="8" v-if="order.delivery_status == 2">
							<div class="btn-item bg-primary" @click="onChangeOrderStatus('delivery_instore')">确认取货</div>
						</van-col>
						<van-col span="8" v-if="order.deliveryer_transfer_status == 1">
							<div class="btn-item bg-primary" @click="onChangeOrderStatus('delivery_transfer')">申请转单</div>
						</van-col>
						<van-col span="8" v-if="order.deliveryer_transfer_status == 1">
							<div class="btn-item bg-danger" @click="onChangeOrderStatus('direct_transfer')">定向转单</div>
						</van-col>
						<van-col span="8" v-if="order.deliveryer_cancel_status == 1">
							<div class="btn-item bg-danger" @click="onChangeOrderStatus('cancel')">取消订单</div>
						</van-col>
					</template>
				</van-row>
			</div>
		</div>
		<iloading v-if="showPreLoading"></iloading>
		<van-dialog
			v-model="code.codeShow"
			show-cancel-button
			confirmButtonText="确认送达"
			@confirm="onChangeOrderStatus('delivery_success')"
			@cancel="code.codeShow = false"
		>	
			<van-field
				placeholder="输入收货码"
				input-align="center"
				readonly
			/>
			<van-field
				v-model="code.value"
				placeholder="请输入收货码"
			/>
		</van-dialog>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import countDown from "@/components/countDown"
	import {getData} from "../../controller/funcCommon"
	import {changeOrderStatus} from "./controller/order.js"
	import {Amap} from '@/config/AMap'
	export default {
		components: {
			publicHeader,
			countDown
		},
		data() {
			return {
				showPreLoading: true,
				order: {},
				deliveryer: {},
				verification_code: 0,
				code: {
					codeShow: false,
					value: ''
				},
				config: {
					map: {
						location_x: '',
						location_y: ''
					}
				}
			}
		},
		methods: {
			onLoad() {
				var that = this;
				getData({
					vue: that,
					url: 'delivery/order/errander/detail',
					data: {id: that.id},
					success: function(res) {
						that.order = res.order;
						that.deliveryer = res.deliveryer;
						that.config = res.config;
						that.verification_code = res.verification_code;
						that.onLoadMap();
					}
				})
			},
			onChangeOrderStatus(type) {
				var that = this;
				if(type == 'delivery_success' && that.verification_code == 1) {
					that.code.codeShow = true;
					if(!that.code.value) {
						return;
					}
				}
				changeOrderStatus({
					vue: that,
					type: type,
					from: 'detail',
					order: that.order,
					code: that.code.value
				})
			},
			onLoadMap() {
				var that = this;
				Amap().then(function(AMap){
					//基本地图加载
					that.map = new AMap.Map('map-container', {
						resizeEnable: true,
						center: [that.config.map.location_y, that.config.map.location_x],//地图中心点
						zoom: 13 //地图显示的缩放级别
					});
					AMap.plugin('AMap.Driving', function() {
						var driving = new AMap.Driving({
							// 驾车路线规划策略，AMap.DrivingPolicy.LEAST_TIME是最快捷模式
							policy: AMap.DrivingPolicy.LEAST_TIME,
							map: that.map,
							hideMarkers: true
						})
						let marker = {};
						let markers = [];
						var startLngLat = [that.deliveryer.location_y, that.deliveryer.location_x];
						var passLngLat = [that.order.buy_location_y, that.order.buy_location_x];
						var endLngLat = [that.order.accept_location_y, that.order.accept_location_x];
						marker = new AMap.Marker({
							position: startLngLat,
							offset: new AMap.Pixel(-13, -31),
							content: '<div class="marker-start-route"></div>'
						});
						markers.push(marker);
						marker = new AMap.Marker({
							position: endLngLat,
							offset: new AMap.Pixel(-13, -31),
							content: '<div class="marker-end-route"></div>'
						});
						markers.push(marker);
						marker = new AMap.Marker({
							position: passLngLat,
							offset: new AMap.Pixel(-13, -31),
							content: '<div class="marker-pass-route"></div>'
						});
						markers.push(marker);
						driving.search(startLngLat, endLngLat, {waypoints: [passLngLat]}, function (status, result) {
							// 未出错时，result即是对应的路线规划方案
							if (status === 'complete') {
								console.log('绘制驾车路线完成')
							} else {
								console.log('获取驾车数据失败：' + result)
							}
						})
						that.map.add(markers);
						that.map.setFitView();
					});
				});
			},
			onSetMapZoom() {
				var that = this;
				//zoom的范围值3-18
				var zoom = that.map.getZoom();
				zoom++;
				that.map.setZoomAndCenter(zoom);
			}
		},
		created() {
			this.query = this.$route.query;
			if(this.query && this.query.id) {
				this.id = this.query.id;
			}
		},
		mounted() {
			this.onLoad();
		}
	}
</script>

<style>
	#paotui-detail #map-container{
		width: 100%;
		height: 200px;
	}
	#paotui-detail .tip{
		padding: 10px 10px;
		font-size: 14px;
		color: #7a7a7a;
	}
	#paotui-detail .handle-type{
		background-color: #FF931E;
		color: #fff;
		font-style: italic;
		padding: 3px 7px 3px 3px;
		font-size: 12px;
		border-radius: 3px;
	}
	#paotui-detail .delivery-time{
		color: #1AB5F5;
		border: 1px solid #1AB5F5;
		font-size: 12px;
		padding: 2px 5px;
		border-radius: 3px;
		margin-left: 10px;
	}
	#paotui-detail .address .address-text{
		flex: 1;
		padding-left: 20px;
		position: relative;
	}
	#paotui-detail .address .address-text:before{
		content: '';
		position: absolute;
		top: 50%;
		left: 0;
		transform: translateY(-50%);
		width: 10px;
		height: 10px;
		border-radius: 50%;
	}
	#paotui-detail .address .address-text.buy-text:before{
		background-color: #f0ad4e;
	}
	#paotui-detail .address .address-text.accept-text:before{
		background-color: #0EC3B3;
	}
	#paotui-detail .address .icon-locationfill{
		width: 30px;
		height: 30px;
		border-radius: 50%;
		border: 1px solid #999;
		font-size: 16px;
		display: flex;
		align-items: center;
		justify-content: center;
	}
	#paotui-detail .tel-group .tel-item{
		flex: 1;
		display: flex;
		align-items: center;
		justify-content: center;
		font-size: 15px;
	}
	#paotui-detail .tel-group .tel-item .img-wrap{
		font-size: 0px;
		width: 25px;
		margin-right: 5px;
	}
	#paotui-detail .goods-imgs .img-wrap{
		width: 100%;
		height: 80px;
		font-size: 0px;
		margin-bottom: 10px;
		border-radius: 3px;
		overflow: hidden;
	}
	#paotui-detail .note{
		padding: 10px;
		border-radius: 3px;
		background-color: #DFFBF9;
		color: #16C5B6;
		display: flex;
		align-items: flex-start;
		justify-content: space-between;
	}
	#paotui-detail .note .note-text{
		flex: 1;
		padding-left: 10px;
		line-height: 1.2;
	}
	#paotui-detail .btn-group .btn-item{
		margin-bottom: 10px;
		padding: 12px 0;
		border-radius: 5px;
		text-align: center;
		font-size: 14px;
	}
	#paotui-detail .marker-end-route{
		position: relative;
		width: 26px;
		height: 31px;
		color: #e90000;
		background: url(../../../static/img/end.png) no-repeat 0 0;
		background-size: cover;
		cursor: pointer;
	}
	#paotui-detail .marker-start-route{
		position: relative;
		width: 26px;
		height: 31px;
		color: #e90000;
		background: url(../../../static/img/start.png) no-repeat 0 0;
		background-size: cover;
		cursor: pointer;
	}
	#paotui-detail .marker-pass-route{
		position: relative;
		width: 26px;
		height: 31px;
		color: #e90000;
		background: url(../../../static/img/ds.png) no-repeat 0 0;
		background-size: cover;
		cursor: pointer;
	}
	#paotui-detail .goods-info{
		position: relative;
	}
	#paotui-detail .goods-info .have-pay{
		position: absolute;
		width: 100px;
		height: 50px;
		left: 50%;
		margin-left: -50px;
		top: 45px;
		background: url("http://1.duoxunwl.com/addons/we7_wmall/static/img/have-pay.png") no-repeat center center;
		background-size: 100% 100%;
	}
	#paotui-detail .goods-info .delivery-pay{
		position: absolute;
		width: 110px;
		height: 70px;
		left: 50%;
		margin-left: -50px;
		top: 45px;
		background: url("http://1.duoxunwl.com/addons/we7_wmall/static/img/delivery-pay.png") no-repeat center center;
		background-size: 100% 100%;
	}
</style>


